





















































































































.core{
  max-height: 200px;
  border: 1px solid #979797;
  border-radius: 5px;
  padding: 10px;
  overflow-y: auto;
  overflow-x: hidden;
  position: relative;
  .abs-zone{
    position: absolute;
    width: 100%;
    height: 100%;
    top:0;
    left: 0;
    background: rgba(255,255,255,.8);
    z-index: 1;
    .box{
      position:absolute;
      width: 100%;
      top:50%;
      left: 0;
      margin-top: -32px;
      text-align: center;
    }
  }
}
